import { usersAPI } from './../api/api'

let initialState = {
  postsData: [
    { id: 0, name: 'React', text: "Hello, it's my first post", likeCount: 12 },
    { id: 0, name: 'React', text: "I'm going to become react developer", likeCount: 20 },
    { id: 2, name: 'Masha', text: "lol", likeCount: 10 }
  ],


  profile: null,
  status: ''


}



const profileReducer = (state = initialState, action) => {

  switch (action.type) {
    case 'ADD-POST':
      
      if (action.text) {
        let newPost = {
          id: 0,
          name: 'React',
          text: action.text,
          likeCount: 0
        }
        return {
          ...state,
          postsData: [...state.postsData, newPost],
        }
      } else {
        return state
      }




    

    case 'SET-PROFILE':
      //debugger
      return ({ ...state, profile: action.profile })

    case 'SET-STATUS':
      //console.log('set status')
      return ({
        ...state,
        status: action.status
      })




    default:
      return state

  }


}

//my post 
export const addPostActionCreator = (text) => ({ type: 'ADD-POST', text })


//profile info
export const setProfile = (profile) => ({ type: 'SET-PROFILE', profile: profile })
export const setStatus = (status) => ({ type: 'SET-STATUS', status: status })
//thunk creators

export const setUserProfileThunkCreator = (userId) => {
  return (dispatch) => {

    dispatch(setProfile(null))

    if (userId) {
      usersAPI.setProfileAPI(userId).then(
        (result) => {

          dispatch(setProfile(result.data))
        }
      )
    }


  }
}

export const setStatusThunkCreator = (status) => {
  return (dispatch) => {
    console.log('setStatusThunkCreator')
    usersAPI.setStatus(status).then((result) => {
      if (result.resultCode === 0) {
        console.log(result.data)
        dispatch(setStatus(result.data))
      }
    })
  }
}

export const getStatusThunkCreator = (id) => {
  return (dispatch) => {
    console.log('getStatusThunkCreator')
    usersAPI.getStatus(id).then(
      (response) => {
        dispatch(setStatus(response.data))
      }
    )
  }
}


export default profileReducer